<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />

    <div class="menu_list" v-for="(item,index) in list">
      <div class="menu_item" @click="toNextPage(index)">
        <div class="menu_name">{{item}}</div>
        <div class="meun_arrow"></div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "detail",
    data(){
      return {
        id:'',
        title:'',
        titles: ['中诚信', '中债资'],
        list: ['业务介绍', '评级报告', '研究报告', '付费报告', '沟通交流']
      }
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      toNextPage(index){
        console.log(index);
      }
    },
    created() {
      this.title = this.titles[this.$route.params.id];
    }
  }
</script>

<style scoped>
  .menu_list {
    padding-left: 15px;
  }
  .menu_item {
    height: 46.5px;
    border-bottom: 0.5px solid rgba(238,238,238,1);
    position: relative;
  }
  .menu_name {
    position: absolute;
    top: 11.5px;
    font-size: 16px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(32, 32, 32, 1);
    line-height: 22.5px;
  }

  .meun_arrow {
    position: absolute;
    top: 17px;
    right: 15px;
    width: 6.5px;
    height: 11.5px;
    background: url("../../assets/images/arrow.png");
    background-size: cover;
  }
</style>



